.app-header {
    background: #000000;
    padding: 0 20px;
    color: white;
    display: flex;
    height: 64px;
    align-items: center;
    justify-items: center;

    box-shadow: 0 4px 6px -1px rgba(255, 255, 255, 0.1),
        0 2px 4px -1px rgba(255, 255, 255, 0.5);
    justify-content: space-between;

    .first-section {
        display: flex;
        align-items: center;
        justify-items: center;
        gap: 3px;

        div {
            width: 50px;
            height: 50px;
            border-radius: 50%;

            img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
            }

        }

    }

    .second-section {
        display: flex;
        gap: 10px;
    }
}


.nice-btn {

    background: linear-gradient(to right,
            $primaryClr,
            darken($primaryClr, 10%));
    border: none;
    color: #fff;
    padding: 10px 20px;
    border-radius: 20px;
    cursor: pointer;
    font-weight: bold;
    transition: background 0.3s, transform 0.3s;

    &:hover {
        background: linear-gradient(to right,
                darken($primaryClr, 10%),
                $primaryClr );
        transform: scale(1.05);
    }

    &:focus {
        outline: none;
    }

}