.products-section {
    padding: 20px;
}

.products {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.product {
    background-color: #ffffff;
    border: 1px solid #e0e0e0;
    border-radius: 5px;
    overflow: hidden;
    max-width: 200px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.product img {
    width: 100%;
    height: 150px;
    object-fit: cover;
}

.product h3 {
    margin: 10px 0;
}

.product p {
    margin: 0;
    padding: 10px;
    background-color: #f8f9fa;
}

.product input[type="checkbox"] {
    margin: 10px;
}

.image-product-div {
    width: 250px;
    height: 250px;


    img {
        width: 100%;
        object-fit: cover;
        height: 100%;
        border-radius: 6px;
    }
}

.filter-div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px;
    background-color: #f4f4f4;
    border: 1px solid #e1e1e1;
    border-radius: 8px;

    button {
        background-color: #4caf50;
        color: #ffffff;
        border: none;
        padding: 10px 15px;
        border-radius: 4px;
        cursor: pointer;
        font-size: 16px;

        &:hover {
            background-color: #45a044;
        }
    }

    select {
        background-color: #ffffff;
        border: 1px solid #e1e1e1;
        padding: 10px;
        border-radius: 4px;
        font-size: 16px;

        option {
            padding: 10px;
        }
    }
}

.filter-input {
    width: 100%;
}