@keyframes slide-in-out {
    0% {
        transform: translateY(150%);
    }

    12% {
        transform: translateY(0);
        opacity: 1;
    }

    90% {
        transform: translateY(0);
        opacity: 1;
    }

    100% {
        transform: translateY(150%);
        opacity: 0;
    }
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeout {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@keyframes scalein {
    from {
        transform: scale(0.6);
    }

    to {
        transform: scale(1);
    }
}

@keyframes scaleout {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(0.6);
    }
}

@keyframes scaleinsoft {
    from {
        transform: scale(0.8);
    }

    to {
        transform: scale(1);
    }
}

// Login animations

@keyframes slide {
    0% {
        transform: translateX(-100%);
    }

    100% {
        transform: translateX(0%);
    }
}

@keyframes zoomInOut {

    0%,
    100% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.05);
    }
}

@keyframes rainbow {

    0%,
    100% {
        color: #ffb6c1;
    }

    10% {
        color: #ffdab9;
    }

    20% {
        color: #fafad2;
    }

    30% {
        color: #98fb98;
    }

    40% {
        color: #87ceeb;
    }

    50% {
        color: #b0e0e6;
    }

    60% {
        color: #dda0dd;
    }

    70% {
        color: #ffc0cb;
    }

    80% {
        color: #20b2aa;
    }

    90% {
        color: #d879f5;
    }
}

@keyframes boxShadowAnimation {
    0% {
        box-shadow: 0 2px 5px rgb(248, 1, 186);
    }

    25% {
        box-shadow: 0 2px 5px rgb(0, 128, 0);
    }

    50% {
        box-shadow: 0 2px 5px rgb(0, 0, 255);
    }

    75% {
        box-shadow: 0 2px 5px rgb(255, 165, 0);
    }

    100% {
        box-shadow: 0 2px 5px rgb(248, 1, 186);
    }
}

//loader

@keyframes rotate {
    100% {
        transform: rotate(360deg);
    }
}








@keyframes bounce {

    0%,
    20%,
    50%,
    80%,
    100% {
        transform: translateY(0);
    }

    40% {
        transform: translateY(-30px);
    }

    60% {
        transform: translateY(-15px);
    }
}

.bounce {
    animation: bounce 2s infinite;
}