.login-signup-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    transition: all 0.3s ease;

    .error-msg {
        margin-block-end: 3px;
        color: red;
    }

    .login-header {
        text-align: center;
        margin-bottom: 20px;
        color: $clr4;
        animation: rainbow 20s linear infinite;
        height: 30px;
        margin-inline-start: 10px;
        font-style: italic;

        &:hover {
            animation: none;
            fill: white;
        }
    }

    .form-group {
        margin-bottom: 15px;
        display: flex;

        align-items: center;
    }

    .btns-group {
        display: flex;
        width: 100%;
        align-items: center;
        // flex-direction: column;
        // gap: 10px;
        justify-content: space-between;

        p {
            text-decoration: underline;
            cursor: pointer;
        }
    }

    input {
        width: 100%;
        padding: 10px;
        border: 2px solid transparent;
        border-radius: 15px;
        background-color: #f5f5f5;
        transition: all 0.3s ease;
        font-size: 1rem;
        outline: none;

        &::placeholder {
            color: #aaa;
        }

        &:focus {
            border-color: $primaryClr;
            box-shadow: 0 0 5px rgba($primaryClr, 0.2);
        }

        &:hover {
            background-color: #f0f0f0;
        }
    }

    .login-button {
        width: 100%;
        padding: 10px;
        background-color: $primaryClr;
        color: #fff;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        transition: background-color 0.3s ease;

        &:hover {
            background-color: darken($primaryClr, 10%);
        }
    }

    p {
        text-align: center;
        color: $clr4;

        span {
            text-decoration: underline;
            transition: color 0.3s ease;
            cursor: pointer;

            &:hover {
                color: $clr6;
            }
        }
    }

    .chat-bot-img,
    .chat-people-img {
        transition: transform 0.3s ease;

        &:hover {
            transform: scale(1.05);
        }
    }
}

.form-container {
    background-color: #fff;
    padding: 20px;
    box-shadow: 0 2px 5px rgb(255, 187, 0);
    border-radius: 5px;
    max-width: 400px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: box-shadow 0.3s ease;

    // &:hover {
    //   box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    // }
}

.chat-people-img-container {
    width: 100%;
    overflow: hidden;
    display: flex;
}

.chat-people-img {
    height: 100px;
    width: 100%;
    animation: slide 10s linear infinite;
    margin-block-end: 10px;
}

.chat-bot-img {
    height: 250px;
    animation: zoomInOut 5s infinite ease; // 5-second duration for a smooth effect
}