.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-add {
    background-color: #fff;
    position: relative;
    width: 100%;
    max-width: 900px;
    height: 500px;
    // max-width: 1000px;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
}

.language-tabs {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.language-tabs button {
    padding: 10px 20px;
    margin: 0 10px;
    border: none;
    background-color: #ddd;
    cursor: pointer;
    transition: background-color 0.3s;
}

.language-tabs button:hover {
    background-color: #bbb;
}

.language-tabs button.active {
    background-color: #888;
    color: #fff;
}

.modal-body {
    margin-top: 24px;
    display: flex;
    gap: 60px;
    // justify-content: space-between;
    width: 100%;
}

.cover-img {
    position: relative;
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.img-edit {
    max-width: 100%;
    height: auto;
}

.loader-img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* Add your loader styling here */
}

.middle-inputs,
.right-inputs {
    display: flex;
    flex-direction: column;
    width: 30%;
}

.middle-inputs label,
.right-inputs label {
    margin-bottom: 10px;
}

.middle-inputs label,
.right-inputs label,
.image-section label,
.product-details-section label {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
}

.image-section {
    width: 300px;
    height: 100%;
    display: flex;
    flex-direction: column;
    // display: flex;
    // gap: 20px;

    h1 {
        margin-top: 16px;
        align-self: center;
    }
}


input[type="text"],
input[type="file"],
textarea {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    box-sizing: border-box;
}


.middle-inputs textarea {
    resize: none;
}

textarea {
    resize: none;
}


.close-product-modal-btn {
    position: absolute;
    margin-bottom: 20px;
    top: 5px;
    right: 20px;
    padding: 10px 20px;
    background-color: #007BFF;
    border: none;
    color: #fff;
    cursor: pointer;
    // background-color: transparent;
    transition: background-color 0.3s;
    border-radius: 6px;

    &:hover {
        background-color: #0056b3;
    }
}

.add-product-modal-btn {
    position: absolute;
    margin-bottom: 20px;
    top: 5px;
    right: 90px;
    border-radius: 6px;
    padding: 10px 20px;
    background-color: #007BFF;
    border: none;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s;

    &:hover {
        background-color: #0056b3;
    }
}

