.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999;
  transition: opacity 0.3s ease;
}

.modal {
  // z-index: 999999999999999999999999999;
  background-color: #ffffff;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.3);
  width: 400px;
  max-width: 80%;
  position: relative;
  transition: transform 0.3s ease, opacity 0.3s ease;
  opacity: 0.9;
  transform: translateY(-10px);
}

.modal p {
  font-size: 18px;
  line-height: 1.5;
  // margin-bottom: 20px;
  color: #333;
  font-family: 'Arial', sans-serif;
}

.close-btn {
  position: absolute;
  width: 30px;
  top: 5px;
  right: 5px;
  background: transparent;
  border: none;
  font-size: 16px;
  color: #888;
  cursor: pointer;
  transition: color 0.3s ease;
  outline: none;
  padding: 0;
  line-height: 1;
}

.close-btn:hover {
  color: #ff4b5c;
}