.station-details {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
}

.station-details h1 {
    font-size: 2em;
    margin-bottom: 20px;
    text-align: center;
}

.subcategories {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.subcategory {
    background-color: #f4f4f4;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    overflow: hidden;
    width: calc(33.33% - 20px);
}

.subcategory h2 {
    font-size: 1.5em;
    margin: 0;
    padding: 10px;
    background-color: #e0e0e0;
    text-align: center;
}

.subcategory img {
    max-width: 100%;
    height: auto;
    display: block;
}

.products {
    padding: 10px;
}

.product {
    border-bottom: 1px solid #e0e0e0;
    margin-bottom: 10px;
    padding-bottom: 10px;
}

.product:last-child {
    border-bottom: none;
}

.product h3 {
    font-size: 1.2em;
    margin: 0 0 10px 0;
}

.product img {
    max-width: 100%;
    height: auto;
    display: block;
    margin-bottom: 10px;
}

.product p {
    margin: 0;
    font-size: 1.2em;
    text-align: right;
}

